* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-image: url("https://img.freepik.com/free-vector/squared-shapes-white-texture_1100-27.jpg?w=740&t=st=1689195848~exp=1689196448~hmac=68aa2a514fdb526836b889f27d8f412d9a099a18c660218d058afd018cd29fd4");
  background-color: #f5f5f5;
}

.content {
  height: 100vh;
 background-size: cover;
  background-position: center;
  padding: 20px;
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/* Transaction page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 255, 0.6);
}

.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 10px 15px;
  background-color: rgba(255, 255, 255, 0.6);
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: #1890ff;
}

.inactive-icon {
  color: rgba(0, 0, 0, 0.4);
}

/* Table */
.ant-table-wrapper {
  background-color: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.ant-table {
  background-color: rgba(255, 255, 255, 0.9);
}

.ant-table-thead > tr > th {
  background-color: #f5f5f5;
}

.ant-table-tbody > tr.ant-table-row:hover > td {
  background-color: #e6f7ff;
}

/* Modal */
.ant-modal-content {
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.ant-modal-header {
  background-color: #f5f5f5;
}

.ant-modal-title {
  color: #000000;
}

/* Button */
.btn {
  background-color: #1890ff;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  padding: 8px 16px;
  font-weight: bold;
  cursor: pointer;
}

.btn-primary {
  background-color: #1890ff;
}

.btn-primary:hover {
  background-color: #40a9ff;
}

/* Spinner */
.spinner {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
