.register-page {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
  }
  
  .register-form {
    width: 400px;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  .register-form h1 {
    font-size: 24px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .register-form .form-item {
    margin-bottom: 20px;
  }
  
  .register-form .form-item label {
    font-weight: bold;
  }
  
  .register-form .form-item input {
    width: 100%;
    padding: 10px;
    border: 1px solid #cccccc;
    border-radius: 5px;
  }
  
  .register-form .form-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .register-form .form-actions a {
    color: #1890ff;
  }
  .bro {
    border: black 1px solid;
    padding: 10px;
    background-color: #f1f1f1;
    border-radius: 5px;
  }
  
  .register-form .form-actions button {
    padding: 10px 20px;
    background-color: #1890ff;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .register-form .form-actions button:hover {
    background-color: #40a9ff;
  }
  .content {
    height: 100vh;
    background-image: url("https://img.freepik.com/free-vector/squared-shapes-white-texture_1100-27.jpg?w=740&t=st=1689195848~exp=1689196448~hmac=68aa2a514fdb526836b889f27d8f412d9a099a18c660218d058afd018cd29fd4");
    background-size: cover;
    background-position: center;
    padding: 20px;
  }